export function words() {
  const getTwentyFiveWords = function() {
    let newList = []
    for (let i = 0; i < 25; i++) {
      const newWord = rando(wordlist);
      newList.push(newWord);
    }
    return newList;
  }

  return getTwentyFiveWords();
}

export function gameCode() {
  return slugify(`${rando(wordlist)}-${rando(wordlist)}-${rando(wordlist)}`);
}